html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font);
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 3rem;
  font-weight: medium;
  line-height: 3rem;
}

h2 {
  font-size: 2.5rem;
  font-weight: medium;
  line-height: 3rem;
  letter-spacing: 0.125rem;
}

h3 {
  font-size: 1.25rem;
  font-weight: medium;
  line-height: 1.625rem;
  letter-spacing: 0.3125rem;
}

body {
  font-weight: normal;
  font-size: 1rem;
  line-height: 1.625rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --peach: hsl(11, 73%, 66%);
  --lightpeach: hsl(11, 100%, 80%);
  --black: hsl(270, 3%, 11%);
  --darkgrey: hsl(264, 5%, 20%);
  --white: hsl(0, 0%, 100%);
  --lightgrey: hsl(210, 17%, 95%);
}

:root {
  --font: "Jost", sans-serif;
}